<template>
    <v-container>
      
      <v-card class="mx-auto px-6 py-8 " max-width="344" color="deep-purple-darken-4">
        <v-form
          v-model="form"
          @submit.prevent="onSubmit"
        >
          <v-text-field
            v-model="account"
            :readonly="loading"
            :rules="[required]"
            class="mb-2 "
            clearable
            label="使用者名稱"
          ></v-text-field>
  
          <v-text-field
            v-model="password"
            :readonly="loading"
            :rules="[required]"
            clearable
            label="密碼"
            placeholder="Enter your password"
          ></v-text-field>
  
          <br>
  
          <v-btn
            :disabled="!form"
            :loading="loading"
            block
            color="success"
            size="large"
            type="submit"
            variant="elevated"
          >
            登入
          </v-btn>
        </v-form>
      </v-card>
    
    </v-container>
  </template>
  
  <script>
  import axios from 'axios';
  const CryptoJS = require("crypto-js");
    export default {
      data: () => ({
        form: false,
        salt:  "kowkoww151s5ww",
          account: null,
        password: null,
        token:'',
        
        loading: false,
      }),
  
      methods: {
        onSubmit () {
          const saltedPassword = this.password + this.salt;
          const hashedPassword = CryptoJS.MD5(saltedPassword).toString();
          if (!this.form) return
          this.loading = true
          axios.post(
            "/api1/login_respond",
            {
              "account":this.account,
              "password":hashedPassword
            },
          )
          .then(res=> {
              console.log(res);
              this.loading = false;
              if(res.status == 200){
                document.cookie = "token=" + res.data.token + "; path=/";
                window.location.replace(`http://20.89.131.34:443//static/dist/home`); //括號內加上+res.data.token
              }
              else
              alert("登入失敗")
          })
          .catch(err=> {
              console.log(err);
              this.loading = false;
              alert('登入失敗');
          })
        },
        required (v) {
        return !!v || 'Field is required'
      },
        
      }
    }
  </script>